onts.scss\";\n\n$black: #151A1E;\n$green: #9EE727;\n$white: #fff;\n$grey: #8b9497;\n$bgDark: #1a1a1a;\n$brown: #484848;\n\n@mixin base-sizes() {\n  &__size {\n    &_l {\n      width: rem(546px);\n    }\n    &_s {\n      width: rem(446px);\n    }\n    &_full {\n      width: 100%;\n      height: rem(42px);\n    }\n  }\n}\n\n@mixin placeholder-color($color) {\n  &::-webkit-input-placeholder {\n    color: $color;\n  }\n\n  &::-moz-placeholder {\n    color: $color;\n  }\n\n  &:-moz-placeholder {\n    color: $color;\n  }\n\n  &:-ms-input-placeholder {\n    color: $color;\n  }\n}\n\n@mixin base-margins() {\n  &__margin {\n    &_left {\n      margin-left: 10px;\n      &_x2 {\n        margin-left: 20px;\n      }\n    }\n    &_right {\n      margin-right: rem(10px);\n      &_x2 {\n        margin-right: rem(20px);\n      }\n    }\n    &_top {\n      margin-top: rem(10px);\n      &_x2 {\n        margin-top: rem(20px);\n      }\n    }\n    &_bottom {\n      margin-bottom: rem(10px);\n      &_x2 {\n        margin-bottom: rem(20px);\n      }\n    }\n  }\n}\n\n@mixin base-aligns() {\n  &__align {\n    &_left {\n      text-align: left;\n    }\n    &_center {\n      text-align: center;\n    }\n    &_right {\n      text-align: right;\n    }\n  }\n}\n\n@mixin base-weight() {\n  &__weight {\n    &_400 {\n      font-weight: 400;\n    }\n    &_500 {\n      font-weight: 500;\n    }\n    &_600 {\n      font-weight: 600;\n    }\n    &_700 {\n      font-weight: 700;\n    }\n    &_800 {\n      font-weight: 800;\n    }\n  }\n}\n\n@mixin main-font($weight) {\n  font-family: \"Montserrat\", sans-serif;\n  font-weight: $weight;\n}\n\nh1,h2,h3,h4,h5,h6,p {\n  padding: 0;\n  margin: 0;\n  font-size: rem(16px);\n}\nul,ol {\n  padding: 0;\n  margin: 0;\n  font-size: 0;\n}\ndiv {\n  &:focus {\n    outline: 0;\n  }\n}\na {\n  text-decoration: none;\n  color: $black;\n  font-size: rem(16px);\n}\nbody {\n  font-size: rem(13px);\n  @include main-font(500);\n  background-color: #151C26;\n  padding: 0;\n  bo