S */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.svg#Montserrat') format('svg'); /* Legacy iOS */\n}\n","@import \"fonts.scss\";\n\n$black: #151A1E;\n$green: #9EE727;\n$white: #fff;\n$grey: #8b9497;\n$bgDark: #1a1a1a;\n$brown: #484848;\n\n@mixin base-sizes() {\n  &__size {\n    &_l {\n      width: rem(546px);\n    }\n    &_s {\n      width: rem(446px);\n    }\n    &_full {\n      width: 100%;\n      height: rem(42px);\n    }\n  }\n}\n\n@mixin placeholder-color($color) {\n  &::-webkit-input-placeholder {\n    color: $color;\n  }\n\n  &::-moz-placeholder {\n    color: $color;\n  }\n\n  &:-moz-placeholder {\n    color: $color;\n  }\n\n  &:-ms-input-placeholder {\n    color: $color;\n  }\n}\n\n@mixin base-margins() {\n  &__margin {\n    &_left {\n      margin-left: 10px;\n      &_x2 {\n        margin-left: 20px;\n      }\n    }\n    &_right {\n      margin-right: rem(10px);\n      &_x2 {\n        margin-right: rem(20px);\n      }\n    }\n    &_top {\n      margin-top: rem(10px);\n      &_x2 {\n        margin-top: rem(20px);\n      }\n    }\n    &_bottom {\n      margin-bottom: rem(10px);\n      &_x2 {\n        margin-bottom: rem(20px);\n      }\n    }\n  }\n}\n\n@mixin base-aligns() {\n  &__align {\n    &_left {\n      text-align: left;\n    }\n    &_center {\n      text-align: center;\n    }\n    &_right {\n      text-align: right;\n    }\n  }\n}\n\n@mixin base-weight() {\n  &__weight {\n    &_400 {\n      font-weight: 400;\n    }\n    &_500 {\n      font-weight: 500;\n    }\n    &_600 {\n      font-weight: 600;\n    }\n    &_700 {\n      font-weight: 700;\n    }\n    &_800 {\n      font-weight: 800;\n    }\n  }\n}\n\n@mixin main-font($weight) {\n  font-family: \"Montserrat\", sans-serif;\n  font-weight: $weight;\n}\n\nh1,h2,h3,h4,h5,h6,p {\n  padding: 0;\n  margin: 0;\n  font-size: rem(16px);\n}\nul,ol {\n  padding: 0;\n  margin: 0;\n  font-size: 0;\n}\ndiv {\n  &:focus {\n    outline: 0;\n  }\n}\na {\n  text-decoration: none;\n  color: $black;\n  font-size: rem(16px);\n}\nbody {\n  font-size: rem(13px);\n  @include main-font(500);\n  background-color: #151C26;\n  padding: 0;\n  box-sizing: border-box;\n  margin: 0;\n  background-repeat: no-repeat;\n  background-position: top center;\n  > * {\n    box-sizing: border-box;\n  }\n}\n\n// Responsive settings\n$screenSizes: (\n        desktop: 1640px,\n        desktop-small: 1440px,\n        desktop-x-small: 1200px,\n        tablet: 991px,\n        mobile: 768px,\n        mobile-small: 480px,\n        mobile-big: 560px,\n);\n\n@mixin screen-gt($name) {\n  @media only screen and (min-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin screen-lt($name) {\n  @media only screen and (max-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin container-size() {\n  width: 95%;\n  margin: auto;\n  padding: 0 rem(20px);\n  @include screen-lt(desktop) {\n    width: 100%;\n  }\n}\n\n\n// Convertation px to rem settings\n$rem-baseline: 16px;\n$rem-fallback: false !default;\n$rem-px-only: false !default;\n@function rem-separator($list, $separator: false) {\n  @if $separator == 'comma' or $separator == 'space' {\n    @return append($list, null, $separator);\n  }\n\n  @if function-exists('list-separator') == true {\n    @return list-separator($list);\n  }\n\n  $test-list: ();\n  @each $item in $list {\n    $test-list: append($test-list, $item, space);\n  }\n\n  @return if($test-list == $list, space, comma);\n}\n\n@mixin rem-baseline($zoom: 100%) {\n  font-size: $zoom / 16px * $rem-baseline;\n}\n\n@function rem-convert($to, $values...) {\n  $result: ();\n  $separator: rem-separator($values);\n\n  @each $value in $values {\n    @if type-of($value) == 'number' and unit($value) == 'rem' and $to == 'px' {\n      $result: append($result, $value / 1rem * $rem-baseline + 0px, $separator);\n    } @else if type-of($value) == 'number' and unit($value) == 'px' and $to == 'rem' {\n      $result: append($result, $value / $rem-baseline + 0rem, $separator);\n    } @else if type-of($value) == 'list' {\n      $value-separator: rem-separator($value);\n      $value: rem-convert($to, $value...);\n      $value: rem-separator($value, $value-separator);\n      $result: append($result, $value, $separator);\n    } @else {\n      $result: append($result, $value, $separator);\n    }\n  }\n\n  @return if(length($result) == 1, nth($result, 1), $result);\n}\n\n@function rem($values...) {\n  @if $rem-px-only {\n    @return rem-convert(px, $values...);\n  } @else {\n    @return rem-convert(rem, $values...);\n  }\n}\n\n@mixin rem($properties, $values...) {\n  @if type-of($properties) == 'map' {\n    @each $property in map-keys($properties) {\n      @include rem($property, map-get($properties, $property));\n    }\n  } @else {\n    @each $property in $properties {\n      @if $rem-fallback or $rem-px-only {\n        #{$property}: rem-convert(px, $values...);\n      }\n      @if not $rem-px-only {\n        #{$property}: rem-convert(rem, $values...);\n      }\n    }\n  }\n}\n\nhtml {\n  @include rem-baseline;\n  @include screen-lt(desktop) {\n    @include rem-baseline(80%);\n  }\n  @include screen-lt(desktop-x-small) {\n    @include rem-baseline(70%);\n  }\n\n}","@import \"./src/theme/global.scss\";\n\n$transitionHover: ease-in 200ms;\n$transitionActive: ease-in 150ms;\n\n$btn-void: $green, $black, #B5FF27, $black, #7CC427, $black, $grey, $black;\n$btn-fill: transparent, $white, #ff510047, $white, transparent, $white, $grey, $white;\n@mixin btn-style(\n  $colorBgDefault,\n  $colorDefault,\n  $colorBgHover,\n  $colorHover,\n  $colorBgActive,\n  $colorActive,\n  $colorBgDisabled,\n  $colorDisabled\n) {\n  background: $colorBgDefault;\n  color: $colorDefault;\n  &:hover {\n    background: $colorBgHover;\n  }\n\n  &:active {\n    background: $colorBgActive;\n    color: $colorActive;\n  }\n\n  &:disabled {\n    background: $colorBgDisabled;\n    cursor: not-allowed;\n    user-select: none;\n  }\n}\n\n.ux-button {\n  @include base-sizes();\n  @include base-margins();\n  height: rem(85px);\n  align-items: center;\n  justify-content: center;\n  border: none;\n  cursor: pointer;\n  display: flex;\n  font-size: rem(36px);\n  border-radius: rem(10px);\n  @include main-font(700);\n  text-transform: uppercase;\n  &:focus {\n    outline: none;\n  }\n\n  &:hover {\n    transition: $transitionHover;\n  }\n\n  &:active {\n    transition: $transitionActive;\n  }\n\n  &__style {\n    &_void {\n      @include btn-style($btn-void...);\n    }\n    &_fill {\n      @include btn-style($btn-fill...);\n      border: 1px solid $green;\n      &:hover {\n        border: 1px solid $green;\n      }\n    }\n  }\n\n}\n"]}]);

